    useEffect(() => {
        let rangeWidth
        if (window.innerWidth >= 480 && window.innerWidth <= 1023) {
            if(inputOne > 4) {
                rangeWidth = (inputOne / 20) * 600;
          
                  } 
                  else {
                   rangeWidth = (inputOne / 20) * 500;
                  }
        } else {

            if (window.innerWidth >= 375 && window.innerWidth <= 479) {
                if(inputOne > 4) {
                    rangeWidth = (inputOne / 20) * 465;
              
                      } 
                      else {
                       rangeWidth = (inputOne / 20) * 500;
                      }
            } 
            else {
        
            if(inputOne > 4) {
                rangeWidth = (inputOne / 20) * 920;
          
                  } 
                  else {
                   rangeWidth = (inputOne / 20) * 770;
                  }
                }
                }
 
        rangeRef.current.style.setProperty("--range-width", `${rangeWidth}px`);
      }, [inputOne]);

