// 
import stile from './Colculator.module.css'
import stileInputTwo from './inputTwo.module.css'
import stileInputOne from './inputOne.module.css'

import React, { useState, useRef, useEffect} from 'react';
import stileFons from './ColculatorFons.module.css'
import Eliipse from './ColculatorFonsImage/Ellipse 996.png'
function Calculator(props) {
    const [inputOne, setInputOne] = useState(1);
    const rangeRef = useRef(null);
    
    const rangeRefTwo = useRef(null);

    const [inputTwo, setInputTwo] = useState(2);

    const handleChange = (e) => {
        setInputOne(parseInt(e.target.value));
    };


    useEffect(() => {
      let rangeWidth
      if (window.innerWidth >= 480 && window.innerWidth <= 1023) {
          if(inputOne > 4) {
              rangeWidth = (inputOne / 20) * 600;
        
                } 
                else {
                 rangeWidth = (inputOne / 20) * 500;
                }
      } else {

          if (window.innerWidth >= 375 && window.innerWidth <= 479) {
              if(inputOne > 4) {
                  rangeWidth = (inputOne / 20) * 465;
            
                    } 
                    else {
                     rangeWidth = (inputOne / 20) * 500;
                    }
          } 
          else {
      
          if(inputOne > 4) {
              rangeWidth = (inputOne / 20) * 920;
        
                } 
                else {
                 rangeWidth = (inputOne / 20) * 770;
                }
              }
              }

      rangeRef.current.style.setProperty("--range-width", `${rangeWidth}px`);
    }, [inputOne]);

    useEffect(() => {
      let inputTwoRange = inputTwo-1
      let rangeWidthTwo
      if (window.innerWidth >= 480 && window.innerWidth <= 1024) {
          if(inputTwo > 4) {
              rangeWidthTwo = (inputTwoRange / 20) * 700;      
                } else {
                  rangeWidthTwo = (inputTwoRange / 20) * 740;
  
                }
      } else {
          if (window.innerWidth >= 375 && window.innerWidth <= 479) {
              if(inputTwo > 4) {
                  rangeWidthTwo = (inputTwoRange / 20) * 520;      
                    } else {
                      rangeWidthTwo = (inputTwoRange / 20) * 550;
      
                    }
          } else {
          if(inputTwo > 4) {
              rangeWidthTwo = (inputTwoRange / 20) * 1000;      
                } else {
                  rangeWidthTwo = (inputTwoRange / 20) * 740;
  
                }
      }}

    
      rangeRefTwo.current.style.setProperty("--range-width", `${rangeWidthTwo}px`);
    }, [inputTwo]);

    const handleChangeTwo = (e) => {
        setInputTwo(parseInt(e.target.value));
    };
    const Money_UpCup = ((inputOne*10000)/4)*inputTwo
    const Money_bank = ((inputOne*10000)*0.03)*inputTwo
    return(
        <div>
            <div>
            <img className={stileFons.Image} src={Eliipse}/>
            </div>
            <div>
                <p className={stile.paragrafOne} ref={props.pageRefFooterSix}>  <div ref={props.pageRefHeaderOne}/></p>
                <h1 className={stile.TextInfo}>
                <pre/>
                         
                <pre className={stile.pre}/>
                 
               </h1>
<p className={stile.paragrafTwo}></p>
            </div>
            <div>
                <div className={stile.CardColculator}/>
                <div>
                    <p className={stile.paragrafThree}> 2.</p>
                    <h1 className={stile.TextInfoTwo}>  </h1>
                    <input
                    className={stileInputOne.VectorInputOne}
        type="range"
        min="1"
        max="10"
        value={inputOne}
        onChange={handleChange}
        ref={rangeRef}
      />
                  <h1 className={stile.VectorTextThree}>1</h1>

                    <p className={stileInputOne.Number2}>2</p>
                    <p className={stileInputOne.Number3}>3</p>
                    <p className={stileInputOne.Number4}>4</p>
                    <p className={stileInputOne.Number5}>5</p>
                    <p className={stileInputOne.Number6}>6</p>
                    <p className={stileInputOne.Number7}>7</p>
                    <p className={stileInputOne.Number8}>8</p>
                    <p className={stileInputOne.Number9}>9</p>
                    <h1 className={stile.VectorTextTwo}>10</h1>

                </div>
                <div>
                    <p className={stile.paragrafFour}> 1.</p>
                    <h1 className={stile.TextInfoThree}>  </h1>
                    <input
    className={stileInputTwo.VectorInputTwo}
    type="range"
    min="2"
    max="10"
    step="2"
    value={inputTwo}
    onChange={handleChangeTwo}
    ref={rangeRefTwo}


/>                    <h1 className={stile.VectorTextOne}>1</h1>

                    <h1 className={stile.VectorTextFour}>2</h1>
                    <h1 className={stile.VectorTextSeven}>4</h1>
                    <h1 className={stile.VectorTextNine}>8</h1>
                    <h1 className={stile.VectorTextTen}>10</h1>


                </div>
                <div>
                    <div className={stile.CardButtonOne}/>
                    <div className={stile.CardButtonTwo}/>

                    <h1 className={stile.CardButtonTextOne}>:</h1>
                    <h1 className={stile.CardButtonTextTwo}>{Money_bank}$</h1>
                    <h1 className={stile.CardButtonTextThree}>:UpCup  </h1>
                    <h1 className={stile.CardButtonTextFour}>{Money_UpCup}$</h1>


                </div>
            </div>
        </div>
    )
}

export default Calculator