import stylesFons from './FeedbackMainFons.module.css'
import ElipseOne from './FeedbackMainFons/Ellipse 997.png'
import ElipseTwo from './FeedbackMainFons/Ellipse 998.png'
import CofeApt from './FeedbackMainImage/3 11.png'
import VectorOne from './FeedbackMainFons/Vector.png'
import VectorTwo from './FeedbackMainFons/Vector (1).png'
import VectorThree from './FeedbackMainFons/Vector (2).png'
import VectorFour from './FeedbackMainFons/Vector (3).png'
import VectorFive from './FeedbackMainFons/Vector (4).png'
import VectorSix from './FeedbackMainFons/Vector (5).png'
import VectorSeven from './FeedbackMainFons/Vector (6).png'
import VectorEight from './FeedbackMainFons/Vector (7).png'
import  StacanOne from './FeedbackMainFons/ 5.png'
import StacanTwo from './FeedbackMainFons/ 6.png'
import StacanThree from './FeedbackMainFons/ 7.png'



import styles from './FeedbackMain.module.css'
import axios from 'axios';
import React, {useState} from 'react'


function FeedbackMain({GET}) {
    const [inputValues, setInputValues] = useState({
        name: "",
        phone: "",
        email: "",
      });
    
      const handleInputChange = (event) => {
        const { name, value } = event.target;
        setInputValues({ ...inputValues, [name]: value });
      };
    
      const handleButtonClick = () => {
        // console.log(inputValues);
        axios.get(`${GET.get}: ${inputValues.name} : ${inputValues.phone} : ${inputValues.email}`)
        .then(function (response) {
          // handle success
          console.log(response);
        })
      };


    return (
<div>
    <div>
        <img src={ElipseOne} className={stylesFons.ElipseOne}/>
        <img src={ElipseTwo} className={stylesFons.ElipseTwo}/>

    </div>
    <div>
        <p className={styles.PoragrahOne}>  ?
    </p>
    <h1 className={styles.TextInfo}>     </h1>
    <form>
    <input
     type ="FeedbackMainName"
      placeholder="      "
     className={styles.InputOne}
     name="name"
     value={inputValues.name}
     onChange={handleInputChange}
     />
    <input
     type="FeedbackMainPhone"
     placeholder="      "
     className={styles.InputTwo}
     name="phone"
     value={inputValues.phone}
     onChange={handleInputChange}
     />
    <input type="FeedbackMainEmail"
     placeholder="     "
     className={styles.InputThree}
     name="email"
     value={inputValues.email}
     onChange={handleInputChange}
     />
    </form>
    <div>
        <div className={styles.Button} onClick={() => {
    handleButtonClick();
  }}/>
        <h1 className={styles.TextButton} onClick={() => {
    handleButtonClick();
  }}> </h1>
    </div>
    <div>
        <img className={styles.CofeApt} src={CofeApt}/>
        <img className={stylesFons.VectorOne} src={VectorOne}/>
        <img className={stylesFons.VectorTwo} src={VectorTwo}/>
        <img className={stylesFons.VectorThree} src={VectorThree}/>
        <img className={stylesFons.VectorFour} src={VectorFour}/>
        <img className={stylesFons.VectorFive} src={VectorFive}/>
        <img className={stylesFons.VectorSix} src={VectorSix}/>
        <img className={stylesFons.VectorSeven} src={VectorSeven}/>
        <img className={stylesFons.VectorEight} src={VectorEight}/>
        <img className={stylesFons.StacanOne} src={StacanOne}/>
        <img className={stylesFons.StacanTwo} src={StacanTwo}/>
        <img className={stylesFons.StacanThree} src={StacanThree}/>

    </div>
    </div>
</div>
    )
}

export default FeedbackMain