//  
import styles from './Feedback.module.css'
import CardRectangleOne from './FeedbackImage/Rectangle 34624155.png'
import Ellipse from './FeedbackImage/Ellipse 982.png'
import Stacan1 from './FeedbackImage/ 4.png'
import VectorFive from './FeedbackImage/Vector.png'
import Stacan2 from './FeedbackImage/ 3.png'
import CardRectangleTwo from './FeedbackImage/Rectangle.png'
import EllipseTwo from './FeedbackImage/EllipseTwo.png'
import Strelka from './FeedbackImage/Vector 675.png'
import Price from './FeedbackImage/8500$.png'
import Star from './FeedbackImage/Star 4.png'
import ImgageCardPhoneTwo from './FeedbackImage/ImagePhone.png'
import ImgageCardPhoneOne from './FeedbackImage/ImagePhoneTwo.png'
import StrelkaTwo from './FeedbackImage/StrelkaTwo.png'
import EllipsTwo from './FeedbackImageFons/Ellipse 967.png'
import VectorOne from './FeedbackImageFons/Vector.png'

import VectorSix from './FeedbackImageFons/Vector (3).png'
import CofeAvt from './FeedbackImage/3 9.png'
import iconOut from './FeedbackImage/icons8--78.png'
import React, { useState } from 'react'
import axios from 'axios';

function Feedback(props) {
    const { GET, pageRefFooterThree, pageRefHeaderThree } = props;

    const [showTextStar, setShowTextStar] = useState(false);
    const [showGetPriceButtonText, setGetPriceButtonText] = useState(false);
    const [showGetPriceButtonTextSend, setGetPriceButtonTextSend,] = useState(false);
    const [inputValues, setInputValues] = useState({
        name: "",
        phone: "",
        email: "",
      });
    
      const handleInputChange = (event) => {
        const { name, value } = event.target;
        setInputValues({ ...inputValues, [name]: value });
      };
    
      const handleButtonClick = () => {
        axios.get(`${GET.get}: ${inputValues.name} : ${inputValues.phone} : ${inputValues.email}`)
        .then(function (response) {
          // handle success
          console.log(response);
        })
      };
    return (
        <div>
            <div>
                <div>
                <img src={VectorOne} className={styles.VectorOne} />

                    {/* <img src={VectorOne} className={styles.VectorOne} /> */}
                    {/* <img src={VectorTwo} className={styles.VectorTwo} /> */}
                    {/* <img src={VectorThree} className={styles.VectorThree} /> */}
                    {/* <img src={VectotFour} className={styles.VectotFour} /> */}
                    <img src={EllipsTwo} className={styles.EllipsTwo} />
                    <img src={EllipsTwo} className={styles.EllipsTwo} />
                    <img src={EllipsTwo} className={styles.EllipsPhoneTwo} />
                    <img src={EllipsTwo} className={styles.EllipsPhoneThree} />
                    <img src={EllipsTwo} className={styles.EllipsPhoneFour} />
                    <img src={EllipsTwo} className={styles.EllipsPhoneFive} />
                    <img src={VectorSix} className={styles.VectorSix} />
                </div>
                <p className={styles.poragraf} ref={props.pageRefFooterThree}>
                    <div ref={props.pageRefHeaderThree}/>
                The cost of a self-service coffee shop                </p>
                <img src={CardRectangleOne} className={styles.ImgageCardOne}></img>
                <img src={ImgageCardPhoneOne} className={styles.ImgageCardPhoneOne}/>

                <img src={Ellipse} className={styles.ElipseOne} />
                <img src={Ellipse} className={styles.ElipseTwo} />
                <img src={Ellipse} className={styles.ElipseThree} />
                <img src={Ellipse} className={styles.ElipseFour} />
                <img src={Ellipse} className={styles.ElipseFive} />

                <h1 className={styles.TextCardOne}>Bright attractive signage
                    <pre className={styles.PreCardOne} />
                    Security camera
                    <pre className={styles.PreCardOne} />
                    Organizer for consumables
                    (tubes, spoons, lids, sugar)
                    <pre className={styles.PreCardOne2} />
                    11 inch touch screen
                    <pre className={styles.PreCardOne3} />
                    3 kinds of syrup to add
                    to your coffee</h1>

                <div>
                    <img src={Stacan1} className={styles.Stacan1} />
                    <img src={VectorFive} className={styles.VectorFive} />
                    <img src={CofeAvt} className={styles.CofeAvt} />
                    <img src={Stacan2} className={styles.Stacan2} />
                </div>
                <img src={CardRectangleTwo} className={styles.CardRectangleTwo} />
                <img src={ImgageCardPhoneTwo} className={styles.ImgageCardPhoneTwo}/>

                <h1 className={styles.TextCardTwo}>Bean coffee machine
                    <pre className={styles.PreCardOne} />
                    Refrigerator for storing milk
                    <pre className={styles.PreCardOne} />
                    Cups of 3 sizes
                    <pre className={styles.PreCardOne} />
                    Payment terminal
                    <pre className={styles.PreCardOne4} />
                    Garbage compartment</h1>
                <img src={EllipseTwo} className={styles.EllipseSix} />
                <img src={EllipseTwo} className={styles.EllipseSeven} />
                <img src={EllipseTwo} className={styles.EllipseEight} />
                <img src={EllipseTwo} className={styles.EllipseNine} />
                <img src={EllipseTwo} className={styles.EllipseTen} />
            </div>
            <div>
                <h1 className={styles.TextDescriptionOne}>The coffee stand is practically self-contained, only one outlet is needed for operation.
               <pre/>
                Connection to the water supply is not required. Water containers are placed in the coffee rack, which are replaced as needed.
</h1>
            </div>

            <div>
                <img src={Strelka} className={styles.Strelka} />
                <img src={Strelka} className={styles.StrelkaOnePhone} />
                <img src={StrelkaTwo} className={styles.StrelkaTwoPhone} />


                <img src={Price} className={styles.Price} />
                <img src={Star} className={styles.star} onMouseEnter={() => setShowTextStar(!showTextStar)}  />

                {showTextStar &&
                    <div className={styles.DescriptionPriceCard} />
                }
                {showTextStar &&
                    <h1 className={styles.DescriptionPriceCardText}>When paying in Israeli shekels, the
    <br/>
                     equivalent of this amount is at the exchange rate on the
                     <br/>
                      day of payment</h1>
                }
                {showTextStar &&
                    <div className={styles.div_star} />
                }
                {showTextStar &&
                    <img src={Star} className={styles.star} onClick={
                        () => {
                            setShowTextStar(!showTextStar)
                        }} />
                }

                <div className={styles.PriceButton} onClick={
                    () => {
                        setGetPriceButtonText(!showGetPriceButtonText)
                    }}/>
               
                <h1 className={styles.PriceButtonText} onClick={
                    () => {
                        setGetPriceButtonText(!showGetPriceButtonText)
                    }}>Leave a request</h1>
                {showGetPriceButtonText &&
                          <div className={styles.modal}>
                                      
                          <form>
                                      <input
                                       type="FeedbackMainName"
                                        placeholder="Name" 
                                        className={styles.InputOne}
                                        value={inputValues.name}
                                        onChange={handleInputChange}
                                        name="name"

                                        />
                                      <input type="FeedbackMainPhone"
                                       placeholder="Phone"
                                        className={styles.InputTwo} 
                                        name="phone"
                                        value={inputValues.phone}
                                        onChange={handleInputChange}
                                        />
                                      <input type="FeedbackMainEmail"
                                       placeholder="Email" 
                                       className={styles.InputThree} 
                                       name="email"
                                       value={inputValues.email}
                                       onChange={handleInputChange}
                                       />
                          </form>
                          <div className={styles.PriceButtonForm} onClick={
                    () => {
                        handleButtonClick();
                        setGetPriceButtonTextSend(!showGetPriceButtonTextSend)
                    }}/>
                          <h1 className={styles.PriceButtonTextForm} onClick={
                    () => {
                        handleButtonClick();
                        setGetPriceButtonTextSend(!showGetPriceButtonTextSend)
                    }}>Leave a request</h1>
              
 {showGetPriceButtonTextSend && 
                     <h1 className={styles.SendMessage}>Sending!</h1>
                     }
                       <img  className={styles.iconsOut} src={iconOut} onClick={
                    () => {
                        setGetPriceButtonText(!showGetPriceButtonText)
                    }}/>
                     
                          </div>
                }
           
            
            </div>
        </div>
    )
}

export default Feedback