//  
import stylesFons from './SchemeWorkFons.module.css'
import styles from './SchemeWork.module.css'


import Elips from './SchemeWorkImageFons/Ellipse 994.png'
import LikeOne from './SchemeWorkImageFons/Group.png'
import LikeTwo from './SchemeWorkImageFons/Group (1).png'
import LikeThree from './SchemeWorkImageFons/Group (2).png'
import LikeFour from './SchemeWorkImageFons/Group (3).png'
import LikeFive from './SchemeWorkImageFons/Group (4).png'
import Rectangle from './SchemeWork/Rectangle 34624181.png'
import Star from './SchemeWork/Star 2.png'
import StarTwo from './SchemeWork/Star 2.png'
import fons from './SchemeWorkImageFons/24537883_flying-coffee-beans-png 2.png'
import VectorOne from './SchemeWork/Vector 676.png'
import VectorTwo from './SchemeWork/Vector 677.png'
import axios from 'axios';

import iconOut from './SchemeWork/icons8--78.png'
import React, {useState} from 'react'
import pdf from './pdf.zip'

function SchemaWork(props) {
  const { GET, pageRefFooterFive, pageRefHeaderFive } = props;

  const handleDownloadClick = () => {
      
    window.open(pdf);

  };
    const [showGetEmailButtonText, setGetEmailButtonText] = useState(false);
    const [showGetPriceButtonTextSend, setGetPriceButtonTextSend,] = useState(false);
    const [inputValues, setInputValues] = useState({
        name: "",
        phone: "",
        email: "",
      });
    
      const handleInputChange = (event) => {
        const { name, value } = event.target;
        setInputValues({ ...inputValues, [name]: value });
      };
    
      const handleButtonClick = () => {
        axios.get(`${GET.get}: ${inputValues.name} : ${inputValues.phone} : ${inputValues.email}`)
    .then(function (response) {
      // handle success
      console.log(response);
    }) 
      };


return (
    <div>
        <div>
    <img src={fons} className={stylesFons.fons}/>
    {/* <div className={stylesFons.fons}/> */}
    <img src={Elips} className={stylesFons.Elips}/>
    <img src={Elips} className={stylesFons.ElipsTwo}/>
    <img src={Elips} className={stylesFons.ElipsThree}/>
    <img src={Elips} className={stylesFons.ElipsFour}/>
    <img src={LikeOne} className={stylesFons.LikeOne}/>
    <img src={LikeTwo} className={stylesFons.LikeTwo}/>
    <img src={LikeThree} className={stylesFons.LikeThree}/>
    <img src={LikeFour} className={stylesFons.LikeFour}/>
    <img src={LikeFive} className={stylesFons.LikeFive}/>


        </div>
        <div>
            <p className={styles.paragraf} ref={props.pageRefFooterFive}>
              <div ref={props.pageRefHeaderFive}/>
              Work scheme</p>
            <div>
                <div className={styles.CardOne}/>
                <h1 className={styles.CardTextOne}>
                You buy a coffee stand.
                <pre className={styles.pre}/>
                We conclude the first contract.


                </h1>
                <img className={styles.Rectangle} src={Rectangle}/>
            </div>

            <div>
            <div className={styles.CardTwo}/> 
            <h1 className={styles.CardTextTwo}>We rent this coffee stand from
            <pre  className={styles.pre}/>
             you with a rental rate of 25%
             <pre  className={styles.pre}/>
              per annum. We enter into
              <pre  className={styles.pre}/>
            a second contract*.</h1>
            <img className={styles.RectangleTwo} src={Rectangle}/>
            </div>

            <div>
            <div className={styles.CardThree}/> 
            <h1 className={styles.CardTextThree}>You receive a fixed monthly 
            <pre className={styles.pre}/>
            payment to the bank account 
            <pre className={styles.pre}/>

            specified in the contract.</h1>
            <img className={styles.RectangleThree} src={Rectangle}/>
            </div>
    <div>
    <div className={styles.CardFour}/> 
    <img src={Star} className={styles.starOne}/>
    <h1 className={styles.CardTextFour}>Important</h1>
    <h1 className={styles.CardTextFive}>
    The rental contract is concluded for 2 years. 
    <pre className={styles.preTwo}/>
     After it expires we can either automatically prolong it or we can buy it back
     <pre className={styles.pre}/>
     from you for 100% of the initial price.
    </h1>
    <img src={VectorOne} className={styles.VectorOne}/>
    </div>

    <div>
    <div className={styles.CardFive}/> 
    <h1 className={styles.CardTextSix}>
        <font color="#FBB03B">Up</font>
        Cup  a turnkey business forma</h1>
        <h1 className={styles.CardTextSeven}>You don't have to think in it:</h1>
        <h1 className={styles.CardTextEight}>
        &nbsp; about finding a location
        <pre className={styles.pre}/>
        &nbsp;cups of coffee sold
        <pre className={styles.pre}/>
        &nbsp;coffee shop service
        
        </h1>
        <img src={VectorOne} className={styles.VectorTwo}/>
    </div>

<div>
<div className={styles.CardSix}/> 
<img src={StarTwo} className={styles.CardImgageStar}/>
<h1 className={styles.CardTextNine}>
You only have to think about which account you will receive
<pre className={styles.pre}/>
your guaranteed money.
</h1>
<img src={VectorOne} className={styles.VectorThree}/>
</div>
    </div>
    <div>
        <div className={styles.buttonImage}onClick={() => {
                setGetEmailButtonText(!showGetEmailButtonText)
            }}/>
      <h1 className={styles.buttonText} onClick={() => {
                setGetEmailButtonText(!showGetEmailButtonText);
            }}>Get sample contracts</h1>
               {showGetEmailButtonText &&
          <div className={styles.modal}>
               <form>
                                      <input 
                                      type="FeedbackMainName" 
                                      name="name"
                                      placeholder="Name"
                                      className={styles.InputOne}
                                      value={inputValues.name}
                                      onChange={handleInputChange}
                                        />

                                      <input
                                       type="FeedbackMainPhone"
                                       name="phone"
                                       placeholder="Phone"
                                       className={styles.InputTwo}
                                       value={inputValues.phone}
                                       onChange={handleInputChange}
                                         />
                                      <input type="FeedbackMainEmail" 
                                      placeholder="Email" 
                                      className={styles.InputThree}
                                      name="email"
                                      value={inputValues.email}
                                      onChange={handleInputChange}
                                       />
                          </form>
                          <div className={styles.PriceButtonForm} onClick={
                    () => {
                      handleButtonClick();
                        setGetPriceButtonTextSend(!showGetPriceButtonTextSend)
                        handleDownloadClick();

                    }}/>
                       <h1 
  className={styles.PriceButtonTextForm} 
  onClick={() => {
    handleButtonClick();
    setGetPriceButtonTextSend(!showGetPriceButtonTextSend);
    handleDownloadClick();
  }}
>
Send
</h1>
              
 {showGetPriceButtonTextSend && 
                     <h1 className={styles.SendMessage}>Sending!</h1>
                     }
                       <img  className={styles.iconsOut} src={iconOut} onClick={
                    () => {
                        setGetEmailButtonText(!showGetEmailButtonText)
                    }}/>
          </div>
        }
    </div>
        </div>
)
}

export default SchemaWork