// 
import stylesBanner from './Banner.module.css'
import ButtonImage from './BannerImage/Button.png'
import ButtonImageColor from './BannerImage/ButtonColor.png'

import Stacan1 from './BannerImage/Stacan1.png'
import Stacan2 from './BannerImage/Stacan2.png'
import Vector from './BannerImage/Vector.png'
import CofeeAvt from './BannerImage/CofeeAvt.png'
import Elipse from './BannerImage/Ellipse 999.png'
import React, {useState} from 'react'
import axios from 'axios';
import iconOut from './BannerImage/icons8--78.png'

import PrezPdf from './PrezentPdf.zip'


function Banner({GET}) {
  const handleDownloadClick = () => {
    
    window.open(PrezPdf);

  };


const [showGetEmailButtonText, setGetEmailButtonText] = useState(false);
const [showGetPriceButtonTextSend, setGetPriceButtonTextSend,] = useState(false);

const [showButtonColor, setShowButtonColor] = useState(false);
const [showDefaultButton, setShowDefaultButton] = useState(true);

const [inputValues, setInputValues] = useState({
    name: "",
    phone: "",
    email: "",
  });

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setInputValues({ ...inputValues, [name]: value });
  };

  const handleButtonClick = () => {
    axios.get(`${GET.get}: ${inputValues.name} : ${inputValues.phone} : ${inputValues.email}`)
    .then(function (response) {
      // handle success
      console.log(response);
    })  };




    return (
        <div>
            <div>
            <h1 className={stylesBanner.descriptionOne}> .
            <font color="#FBB03B"> <b>   </b> </font>
            </h1>
            <p className={stylesBanner.poragraf}>   </p>
            <h1 className={stylesBanner.descriptionTwo}>     
            <font color="#FBB03B"><b>   </b></font>
             </h1>

     
            <div
  onMouseEnter={() => {
    setShowButtonColor(true);
    setShowDefaultButton(false);
  }}
  onMouseLeave={() => {
    setShowButtonColor(false);
    setShowDefaultButton(true);
  }}
>
  {showDefaultButton && (
    <div>
    <div
      //       
      className={stylesBanner.Button}
      onClick={() => {
        setGetEmailButtonText(!showGetEmailButtonText);
      }}
    />
    <h1
      //       
      className={stylesBanner.ButtonText}
      onClick={() => {
        setGetEmailButtonText(!showGetEmailButtonText);
      }}
    >
       
    </h1>
    <img
      //    
      src={ButtonImage}
      className={stylesBanner.ButtonTextTwo}
    ></img>
    </div>
  )}

  {showButtonColor && (
    <div>
      <div
        //       
        className={stylesBanner.ButtonColor}
        onClick={() => {
          setGetEmailButtonText(!showGetEmailButtonText);
        }}
      />
      <h1
        //       
        className={stylesBanner.ButtonTextColor}
        onClick={() => {
          setGetEmailButtonText(!showGetEmailButtonText);
        }}
      >
         
      </h1>
      <img
        //    
        src={ButtonImageColor}
        className={stylesBanner.ButtonTextTwoColor}
      ></img>
    </div>
  )}
</div>

 
            
                 {showGetEmailButtonText &&
          <div className={stylesBanner.modal}>
               <form>
                                      <input 
                                      type="FeedbackMainName" 
                                      name="name"
                                      placeholder=""
                                      className={stylesBanner.InputOne}
                                      value={inputValues.name}
                                      onChange={handleInputChange}
                                        />

                                      <input
                                       type="FeedbackMainPhone"
                                       name="phone"
                                       placeholder=" "
                                       className={stylesBanner.InputTwo}
                                       value={inputValues.phone}
                                       onChange={handleInputChange}
                                         />
                                      <input type="FeedbackMainEmail" 
                                      placeholder="" 
                                      className={stylesBanner.InputThree}
                                      name="email"
                                      value={inputValues.email}
                                      onChange={handleInputChange}
                                       />
                          </form>
                          <div className={stylesBanner.PriceButtonForm} onClick={
                    () => {
                      handleButtonClick();
                        setGetPriceButtonTextSend(!showGetPriceButtonTextSend)
                        handleDownloadClick()
                    }}/>
                       <h1 
  className={stylesBanner.PriceButtonTextForm} 
  onClick={() => {
    handleButtonClick();
    setGetPriceButtonTextSend(!showGetPriceButtonTextSend);
    handleDownloadClick();
  }}
>
   
</h1>
              
 {showGetPriceButtonTextSend && 
                     <h1 className={stylesBanner.SendMessage}>!</h1>
                     }
                       <img  className={stylesBanner.iconsOut} src={iconOut} onClick={
                    () => {
                        setGetEmailButtonText(!showGetEmailButtonText)
                    }}/>
          </div>
        }
            <h1 className={stylesBanner.descriptionTree}>  UpCup</h1>
            </div>
            <div>
        <img src={Vector} className={stylesBanner.Vector}></img>
        <img src={Stacan2} className={stylesBanner.Stacan2}></img>
        <img src={CofeeAvt} className={stylesBanner.CofeeAvt}></img>
        <img src={Stacan1} className={stylesBanner.Stacan1}></img>
            </div>
        <img src={Elipse} className={stylesBanner.Elipse}></img>
        <img src={Elipse} className={stylesBanner.ElipsePhoneOne}></img>
        <img src={Elipse} className={stylesBanner.ElipsePhoneTwo}></img>

        </div>
     
    )
}

export default Banner