import Headers from './components-efret/headers/header'
import Banner from './components-efret/Banner/Banner'
import CompanyDescription from './components-efret/CompanyDescription/CompanyDescription'
import Feedback from './components-efret/Feedback/Feedback'
import Services from './components-efret/Services/Services'
import SchemaWork from './components-efret/SchemeWork/SchemeWork'
import Calculator from './components-efret/Calculator/calculator'
import FeedbackMain from './components-efret/FeedbackMain/FeedbackMain'
import Footer from './components-efret/footer/Footer'

import React, { useRef } from 'react';

function Hebrew() {
  const pageRefFooterOne = useRef(null); 
  const pageRefFooterTwo = useRef(null); 
  const pageRefFooterThree = useRef(null); 
  const pageRefFooterFour = useRef(null); 
  const pageRefFooterFive = useRef(null); 
  const pageRefFooterSix = useRef(null); 

  const pageRefHeaderOne = useRef(null); 
  const pageRefHeaderTwo = useRef(null); 
  const pageRefHeaderThree = useRef(null); 
  const pageRefHeaderFour = useRef(null); 
  const pageRefHeaderFive = useRef(null); 
  const pageRefHeaderSix = useRef(null); 

  let token = ''
  let chat_id = ''
  let GET = `https://api.telegram.org/${token}/sendMessage?chat_id=${chat_id}&text=`
  return (
    <div>
   <Headers pageRefFooterOne={pageRefFooterOne} 
   pageRefHeaderOne={pageRefHeaderOne}
   pageRefHeaderTwo={pageRefHeaderTwo}
   pageRefHeaderThree={pageRefHeaderThree}
   pageRefHeaderFour={pageRefHeaderFour}
   pageRefHeaderFive={pageRefHeaderFive}
   pageRefHeaderSix={pageRefHeaderSix}
   /> {/*Header*/}
   <Banner GET={{get: GET}} /> {/**/}
   <CompanyDescription pageRefFooterTwo={pageRefFooterTwo}  pageRefHeaderTwo={pageRefHeaderTwo}/> {/* */}
   <Feedback GET={{get: GET}} pageRefFooterThree={pageRefFooterThree} pageRefHeaderThree={pageRefHeaderThree}/> {/* */}
   <Services pageRefFooterFour={pageRefFooterFour} pageRefHeaderFour={pageRefHeaderFour}/> {/**/}
   <SchemaWork GET={{get: GET}} pageRefHeaderFive={pageRefHeaderFive} pageRefFooterFive={pageRefFooterFive}/> {/* */}
   <Calculator pageRefFooterSix={pageRefFooterSix} pageRefHeaderOne={pageRefHeaderSix}/> {/**/}
   <FeedbackMain GET={{get: GET}}/> 
   <Footer pageRefFooterOne={pageRefFooterOne}
    pageRefFooterTwo={pageRefFooterTwo}
     pageRefFooterThree={pageRefFooterThree}
     pageRefFooterFour={pageRefFooterFour}
     pageRefFooterFive={pageRefFooterFive}
     pageRefFooterSix={pageRefFooterSix}
     />
    </div>
  );
}

export default Hebrew;
